@import url(https://fonts.googleapis.com/css?family=Titillium+Web:200,200i,300,300i,400,400i,600,600i,700,700i,900&subset=latin-ext);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;600;700&display=swap);
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Titillium Web", sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.menu {
  font-family: "Titillium Web", sans-serif;
  font-weight: bold;
  background: #eae8e9;
}

.buscador {
  position: relative;
}

@media (max-width: 575px) {
  .buscador {
    position: relative;
    width: 200px;
    margin-left: -5px;
  }
}

.lupa {
  position: absolute;
  margin-left: 125px;
}

#redesSociles {
  position: absolute;
  z-index: 2;
  margin-top: 20px;
  margin-left: -25px;
}

@media (max-width: 575px) {
  #redesSociles {
    margin-left: 10px;
  }
}

#redesSociles img {
  height: 20px;
}

#carousel h2 {
  font-family: 'Open Sans', sans-serif;
  font-weight: bold;
  margin-bottom: 80px;
}

@media (max-width: 1131px) {
  #carousel h2 {
    margin-bottom: 50px;
  }
}

@media (max-width: 1122px) {
  #carousel h2 {
    margin-bottom: 20px;
  }
}

@media (max-width: 1142px) {
  #carousel h2 {
    margin-bottom: 0px;
  }
}

@media (max-width: 850px) {
  #carousel img {
    height: 400px;
    object-fit: cover;
  }
}

#recuadros {
  margin-top: -5px;
}
.footer {
  background: #014995;
  position: relative;
  color: white;
}

#whatsapp {
  position: relative;
  z-index: absolute;
  height: 40px;
  margin-top: 20px;
}

